.programs {
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 74px;
  }
}

.programs__container {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.programs__wrapper {
  margin-bottom: 22px;
  background-color: #f2f2f2;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    padding-bottom: 36px;
    margin-bottom: 30px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    width: 570px;
  }
}

.programs__wrapper:last-child {
  margin-bottom: 8px;

  @media (min-width: $tablet-width) {
    margin-bottom: 24px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.programs__list {
  margin: 0;
  padding: 0;
}

.programs__wrapper:first-of-type .programs__item {
  position: relative;

  &::after {
    position: absolute;
    top: 21px;
    left: 29px;
    content: "";
    width: 36px;
    height: 50px;
    background-image: url("../img/main-page/cat-slim.svg");
    background-size: cover;
  }

  &::before {
    position: absolute;
    top: 24px;
    left: 21px;
    content: "";
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #68b738;
  }

  @media (min-width: $tablet-width) {
    &::after {
      top: 5px;
      width: 140px;
      height: 194px;
      left: auto;
      right: 92px;
    }

    &::before {
      top: 16px;
      left: auto;
      right: 62px;
      width: 200px;
      height: 200px;
    }
  }

  @media (min-width: $desktop-width) {
    &::after {
      top: -38px;
      width: 70px;
      height: 97px;
      left: 67px;
    }

    &::before {
      top: -32px;
      width: 100px;
      height: 100px;
      left: 52px;
      right: 62px;
    }
  }
}

.programs__wrapper:nth-of-type(2) .programs__item {
  position: relative;

  &::after {
    position: absolute;
    top: 35px;
    left: 13px;
    content: "";
    width: 67px;
    height: 28px;
    background-image: url("../img/main-page/cat-strong.svg");
    background-size: cover;
  }

  &::before {
    position: absolute;
    top: 24px;
    left: 22px;
    content: "";
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #68b738;
  }

  @media (min-width: $tablet-width) {
    &::after {
      top: 59px;
      width: 268px;
      height: 112px;
      left: auto;
      right: 28px;
    }

    &::before {
      top: 16px;
      left: auto;
      right: 60px;
      width: 200px;
      height: 200px;
    }
  }

  @media (min-width: $desktop-width) {
    &::after {
      top: -11px;
      width: 134px;
      height: 56px;
      left: 33px;
      right: auto;
    }

    &::before {
      top: -32px;
      width: 100px;
      height: 100px;
      left: 52px;
      right: 62px;
    }
  }
}

.programs__description {
  padding-top: 30px;
  padding-left: 91px;
  margin-bottom: 29px;
  font-family: $title-font;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: $black;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
    padding: 0;
    margin: 77px 200px 24px 53px;
  }

  @media (min-width: $desktop-width) {
    margin: 77px 0 73px 0;
    padding-left: 214px;
  }
}

.programs__button-catalog {
  position: relative;
  padding-bottom: 20px;
  display: block;
  width: 134px;
  margin-top: 20px;
  margin-left: 20px;
  text-decoration: none;
  font-family: $title-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: $black;
  cursor: pointer;

  &::after {
    position: absolute;
    content: "";
    top: 2px;
    right: 1px;
    width: 32px;
    height: 10px;
    background-image: url("../img/main-page/arrow.svg");
    background-repeat: no-repeat;
    background-position: -8px;

    @media (min-width: $tablet-width) {
      top: 10px;
      right: -30px;
    }
  }

  &:hover::after {
    background-position: 0;
    transition: 0.6s;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-left: 52px;
    font-size: 20px;
    line-height: 30px;
  }
}

.programs__definition {
  margin-left: 20px;
  padding-bottom: 21px;
  color: #444444;
  width: 227px;
  border-bottom: solid 1px #d9d9d9;

  @media (min-width: $tablet-width) {
    padding: 0;
    font-size: 16px;
    line-height: 24px;
    width: 340px;
    margin-left: 52px;
    margin-bottom: 53px;
    border-bottom: none;
  }

  @media (min-width: $desktop-width) {
    width: 467px;
    margin-bottom: 27px;
  }
}

.programs__image {
  margin-left: 17px;

  @media (min-width: $tablet-width) {
    margin-left: 25px;
  }
}
